<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-table :data="rightsList" border stripe>
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column label="" prop="authName"></el-table-column>
        <el-table-column label="" prop="path"></el-table-column>
        <el-table-column label="" prop="level">
          <template slot-scope="scope">
              <el-tag v-show="scope.row.level==='0'"></el-tag>
              <el-tag type="success" v-show="scope.row.level==='1'"></el-tag>
              <el-tag type="warning" v-show="scope.row.level==='2'"></el-tag>

          </template>

        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
  export default {
    data(){
    return{
      rightsList:[]
    }
    },
    created() {
      this.getRightsList()
    },
    methods:{
      async getRightsList(){
        const { data: res } = await this.$http.get('rights/list')
        if (res.meta.status !== 200) {
          this.$message.error('')
        }
        this.rightsList=res.data
      },
    }
  }
</script>

<style lang="less" scoped>

</style>
