<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
        <el-alert title="" show-icon :closable="false" type="warning"></el-alert>
      <el-row>
        <el-col>
          <span></span>
          <el-cascader expand-trigger="hover" :options="catelist"
                       :props="cateProps" v-model="selectedCateKeys" @change="handleChanged" clearable change-on-select>
          </el-cascader>
        </el-col>
      </el-row>

<!--      -->
        <el-tabs v-model="activeName" @tab-click="handleTabClick">
          <el-tab-pane label="" name="many">
            <el-button  :disabled="idBtnDisable" size="mini" type="primary" @click="addDialogVisible=true"></el-button>
            <el-table :data="manyTableData" border stripe>
              <el-table-column  type="expand">
                <template slot-scope="scope">
                    <el-tag v-for="(item,i) in scope.row.attr_vals" :key="i" closable @close="handleClose(i,scope.row)">{{item}}</el-tag>
                  <!--  -->
                  <el-input class="input-new-tag" v-if="scope.row.inputVisible"
                            v-model="scope.row.inputValue" ref="saveTagInput" size="small"
                            @keyup.enter.native="handleInputConfirm(scope.row)" @blur="handleInputConfirm(scope.row)">
                  </el-input>
                  <!--  -->
                  <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>

                </template>
              </el-table-column>
              <el-table-column  label="#" type="index"></el-table-column>
              <el-table-column label="" prop="attr_name"></el-table-column>
              <el-table-column label="">
                <template slot-scope="scope">
                  <el-button type="primary" size="mini" icon="el-icon-edit" @click="showEditDialog(scope.row.attr_id)"></el-button>
                  <el-button type="danger" size="mini" icon="el-icon-delete" @click="removeParams(scope.row.attr_id)"></el-button>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>

          <el-tab-pane label="" name="only">
            <el-button :disabled="idBtnDisable" size="mini" type="primary" @click="addDialogVisible=true"></el-button>
            <el-table :data="onlyTableData" border stripe>
              <el-table-column  type="expand">
                <template slot-scope="scope">
                  <el-tag v-for="(item,i) in scope.row.attr_vals" :key="i" closable @close="handleClose(i,scope.row)">{{item}}</el-tag>
                  <!--  -->
                  <el-input class="input-new-tag" v-if="scope.row.inputVisible"
                            v-model="scope.row.inputValue" ref="saveTagInput" size="small"
                            @keyup.enter.native="handleInputConfirm(scope.row)" @blur="handleInputConfirm(scope.row)">
                  </el-input>
                  <!--  -->
                  <el-button v-else class="button-new-tag" size="small" @click="showInput(scope.row)">+ New Tag</el-button>

                </template>
              </el-table-column>
              <el-table-column  label="#" type="index"></el-table-column>
              <el-table-column label="" prop="attr_name"></el-table-column>
              <el-table-column label="">
                <template slot-scope="scope">
                  <el-button type="primary" size="mini" icon="el-icon-edit" @click="showEditDialog(scope.row.attr_id)"></el-button>
                  <el-button type="danger" size="mini" icon="el-icon-delete" @click="removeParams(scope.row.attr_id)"></el-button>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
        </el-tabs>


    </el-card>
<!--    -->
    <el-dialog
      :title="''+titleText"
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addDialogColsed">
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="100px">
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="addForm.attr_name"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
    <el-button @click="addDialogVisible = false"> </el-button>
    <el-button type="primary" @click="addParams"> </el-button>
  </span>
    </el-dialog>
<!--  -->
    <el-dialog
      :title="''+titleText"
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogColsed">
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="100px">
        <el-form-item :label="titleText" prop="attr_name">
          <el-input v-model="editForm.attr_name"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
    <el-button @click="editDialogVisible = false"> </el-button>
    <el-button type="primary" @click="editParams"> </el-button>
  </span>
    </el-dialog>

  </div>
</template>

<script>
  export default {
    data() {
      return {
        catelist:[],
        cateProps:{
          value: 'cat_id',
          label: 'cat_name',
          children: 'children'
        },
        selectedCateKeys:[],
        activeName:'many',
        manyTableData:[],
        onlyTableData:[],
        addDialogVisible:false,
        addForm: {
          attr_name: ''
        },
        // 
        addFormRules: {
          attr_name: [
            { required: true, message: '', trigger: 'blur' }
          ]
        },
        editDialogVisible:false,
        editForm: {},
        // 
        editFormRules: {
          attr_name: [
            { required: true, message: '', trigger: 'blur' }
          ]
        }
      }
    },
    created() {
      this.getCateList()
    },
    computed:{
      idBtnDisable() {
        if (this.selectedCateKeys.length !== 3) {
          return true
        }
        return false
      },
      cateId(){
        if(this.selectedCateKeys.length===3){
          return this.selectedCateKeys[2]
        }
        return null
      },
      titleText(){
        if(this.activeName==='many'){
          return ''
        }
        return  ''
      }
    },
    methods:{
    async getCateList(){
      const {data:res} = await this.$http.get('categories')
        if(res.meta.status!==200) this.$message.error("")
          this.catelist=res.data
      },
      handleChanged(){
        this.getParamsData()
      },
      handleTabClick(){
        this.getParamsData()
      },
      // 
      async getParamsData() {
        // 
        if (this.selectedCateKeys.length !== 3) {
          this.selectedCateKeys = []
          this.manyTableData = []
          this.onlyTableData = []
          return
        }
        // Id
        const { data: res } = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          {
            params: { sel: this.activeName }
          }
        )

        if (res.meta.status !== 200) {
          return this.$message.error('')
        }

        res.data.forEach(item => {
          item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
          // 
          item.inputVisible = false
          // 
          item.inputValue = ''
        })
        if (this.activeName === 'many') {
          this.manyTableData = res.data
        } else {
          this.onlyTableData = res.data
        }
      },
      addDialogColsed(){
        this.$refs.addFormRef.resetFields()
      },
      addParams(){
        this.$refs.addFormRef.validate(async valid => {
          if (!valid) return
          const { data: res } = await this.$http.post(
            `categories/${this.cateId}/attributes`,
            {
              attr_name: this.addForm.attr_name,
              attr_sel: this.activeName
            }
          )

          if (res.meta.status !== 201) {
            this.$message.error('')
          }

          this.$message.success('')
          this.addDialogVisible = false
          this.getParamsData()
        })
      },
      async showEditDialog(attr_id){
        const { data: res } = await this.$http.get(
          `categories/${this.cateId}/attributes/${attr_id}`,
          {
            params: { attr_sel: this.activeName }
          }
        )

        if (res.meta.status !== 200) {
          return this.$message.error('')
        }

        this.editForm = res.data
        this.editDialogVisible = true      },
      editDialogColsed(){
        this.$refs.editFormRef.resetFields()
      },
      editParams(){
        this.$refs.editFormRef.validate(async valid => {
          if (!valid) return
          const { data: res } = await this.$http.put(
            `categories/${this.cateId}/attributes/${this.editForm.attr_id}`,
            { attr_name: this.editForm.attr_name, attr_sel: this.activeName }
          )

          if (res.meta.status !== 200) {
            return this.$message.error('')
          }

          this.$message.success('')
          this.getParamsData()
          this.editDialogVisible = false
        })
      },
     async removeParams(attr_id){
        const confirmResult = await this.$confirm(
          ', ?',
          '',
          {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }
        ).catch(err => err)


       if(confirmResult!=='confirm') {
        return  this.$message.error("")
       }
       const { data: res } = await this.$http.delete(
         `categories/${this.cateId}/attributes/${attr_id}`
       )
        if(res.meta.status!==200) {
          this.$message.error('')
        }
       this.$message.success('')
        this.getParamsData()
      },
      handleInputConfirm(row){
      if(row.inputValue.trim().length===0){
        row.inputValue=''
        row.inputVisible=false
        return
      }
        row.attr_vals.push(row.inputValue.trim())
        row.inputValue=''
        row.inputVisible=false
        this.saveAttrVals(row)

      },
      async saveAttrVals(row) {
        // 
        const { data: res } = await this.$http.put(
          `categories/${this.cateId}/attributes/${row.attr_id}`,
          {
            attr_name: row.attr_name,
            attr_sel: row.attr_sel,
            attr_vals: row.attr_vals.join(' ')
          }
        )

        if (res.meta.status !== 200) {
          return this.$message.error('')
        }

        this.$message.success('')
      },
      showInput(row){
          row.inputVisible=true
        this.$nextTick(_ => {
          this.$refs.saveTagInput.$refs.input.focus()
        })
      },
      handleClose(i,row){
          row.attr_vals.splice(i,1)
          this.saveAttrVals(row)
      }
    }
  }
</script>

<style lang="less" scoped>
  .el-row{
    margin: 15px 0;
    margin-left: 20px;
  }
 .el-tag{
   margin: 10px;
 }
  .input-new-tag{
    width: 120px;
  }
</style>
